html {
  font-family: Roboto, sans-serif;
}

body {
  --color-active-link: #00acdc;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.linkImgLogo {
  align-items: center;
  display: flex;
}

.imgLogo {
  width: 40px;
  height: 40px;
}

.nav {
  margin: 0;
}

.nav_menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_style {
  margin-right: 20px;
}

.nav_style:last-child {
  margin: 0;
}

.nav_link {
  text-transform: uppercase;
  white-space: nowrap;
  align-items: center;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:link {
  color: #000;
}

.nav_link:hover, .nav_link.is-active {
  color: var(--color-active-link);
}

.nav_link.is-active:after {
  content: "";
  background-color: var(--color-active-link);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
